'use client'

import { useEffect, useState } from 'react';
import { ChevronLeft, ChevronRight } from 'lucide-react'; // Asegúrate de importar los iconos
import { Swiper, SwiperSlide } from 'swiper/react'; // Importa Swiper y SwiperSlide desde swiper/react
import { Navigation, Autoplay } from 'swiper'; // Importa los módulos Navigation y Autoplay desde swiper
import 'swiper/swiper-bundle.css'; // Asegúrate de que los estilos estén bien importados

// Importar las funciones de API que creamos antes
import { fetchFeaturedMovies, fetchGenres, fetchMoviesByGenre } from '@/lib/api';

export default function MainDashboard() {
  const [featuredMovies, setFeaturedMovies] = useState([]);
  const [filterMovies, setFilterMovies] = useState({});
  const [genreFilters, setGenreFilters] = useState([]);

  // Cargar las películas y géneros
  useEffect(() => {
    const loadMovies = async () => {
      // Obtener películas destacadas
      const movies = await fetchFeaturedMovies();
      setFeaturedMovies(movies);

      // Obtener géneros
      const genres = await fetchGenres();
      setGenreFilters(genres);

      // Cargar las películas por género
      const moviesByGenre = {};
      for (const genre of genres) {
        const moviesForGenre = await fetchMoviesByGenre(genre.id);
        moviesByGenre[genre.name] = moviesForGenre;
      }
      setFilterMovies(moviesByGenre);
    };

    loadMovies();
  }, []);

  return (
    <div className="p-8 text-white bg-black">
      {/* Carrusel de fondo de películas */}
      <div className="relative">
        <Swiper
          spaceBetween={20}
          slidesPerView={1}
          autoplay={{ delay: 5000 }}
          navigation={true} // Habilitar navegación
          modules={[Navigation, Autoplay]} // Incluir los módulos necesarios
          className="h-96"
        >
          {featuredMovies.map((movie) => (
            <SwiperSlide key={movie.id}>
              <img
                src={`https://image.tmdb.org/t/p/original${movie.poster_path}`}
                alt={movie.title}
                className="w-full h-full object-cover"
              />
            </SwiperSlide>
          ))}
        </Swiper>
        <button className="absolute left-4 top-1/2 -translate-y-1/2 z-10 text-white">
          <ChevronLeft className="w-10 h-10" />
        </button>
        <button className="absolute right-4 top-1/2 -translate-y-1/2 z-10 text-white">
          <ChevronRight className="w-10 h-10" />
        </button>
      </div>

      {/* Carruseles por filtros */}
      <div className="mt-12">
        <h2 className="text-2xl font-bold">Películas por Género</h2>
        <div className="space-y-12">
          {genreFilters.map((genre) => (
            <div key={genre.id}>
              <h3 className="text-xl font-semibold">{genre.name}</h3>
              <Swiper spaceBetween={20} slidesPerView={6} modules={[Navigation]} className="mt-6">
                {filterMovies[genre.name]?.map((movie) => (
                  <SwiperSlide key={movie.id}>
                    <img
                      src={`https://image.tmdb.org/t/p/w300${movie.poster_path}`}
                      alt={movie.title}
                      className="w-full h-48 object-cover rounded-lg"
                    />
                    <div className="mt-2 text-center">
                      <p className="font-semibold">{movie.title}</p>
                      <p className="text-sm">{movie.vote_average} ⭐</p>
                    </div>
                  </SwiperSlide>
                ))}
              </Swiper>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
